import noUiSlider from 'nouislider';
import { exist } from '../utils';
import 'nouislider/dist/nouislider.css';

import 'ScssComponents/space-filter.scss';

const spaceFilter = (min, max, step) => {
	const $valuesSlider = document.querySelector('.js-space-filter-slider');
	const $valuesTitle = document.querySelector('.js-space-filter-title');
	const $getResultsBlock = document.querySelector('.js-venue-result');

	const $getAttrCapacity = document.querySelectorAll('[data-capacity]');
	const $inputCapacity = document.querySelector('.js-space-filter-title');

	let filteredItem = $getAttrCapacity.length;

	function printResultVelue() {
		const $hiddenItem = document.querySelectorAll('.venue_spaces__item--hidden_mod');

		if (!exist($getResultsBlock)) return;

		$getResultsBlock.textContent = `${filteredItem - $hiddenItem.length}`;

		if ($getResultsBlock.textContent === '0') {
			$getResultsBlock.parentElement.classList.add('venue_spaces__filter_result--show_mod');
		} else {
			$getResultsBlock.parentElement.classList.remove('venue_spaces__filter_result--show_mod');
		}
	}

	function filterItem() {
		if (!exist($getAttrCapacity) && !exist($inputCapacity)) return;

		const getValue = parseInt($inputCapacity.value.match(/\d+/g), 10);

		$getAttrCapacity.forEach(($item) => {
			const itemCapacity = $item.dataset.capacity;
			const inputCapacity = parseInt(getValue, 10);

			if (itemCapacity <= inputCapacity) {
				$item.classList.add('venue_spaces__item--hidden_mod');
			} else {
				$item.classList.remove('venue_spaces__item--hidden_mod');
			}
		});
	}

	if ($valuesSlider && $valuesTitle) {
		noUiSlider.create($valuesSlider, {
			start: [10],
			connect: 'lower',
			range: {
				min,
				max,
			},
			step,
		});

		$valuesSlider.noUiSlider.on('update', (values) => {
			const value = parseInt(values[0]);
			$valuesTitle.value = `${value} Guests`;

			filterItem();
			printResultVelue();
		});
	}
};

export default spaceFilter;
