import Swiper, { Scrollbar } from 'swiper';
import { onWindowResize } from 'utils/index';
import { BREAKPOINTS } from 'utils/constants';
import 'swiper/css';
import 'swiper/css/scrollbar';

import spaceFilter from './space-filter';
import 'ScssComponents/venue-spaces.scss';
import 'ScssComponents/event-card.scss';

const venueSpaces = (
	sectionSelector = '.js-venue-spaces-section',
	sliderSelector = '.js-venue-spaces-slider',
	scrollbar = '.swiper-scrollbar',
) => {
	const $sections = document.querySelectorAll(sectionSelector);
	if (!$sections) return;

	$sections.forEach(($section) => {
		if ($section.classList.contains('venue_spaces--slider_mod')) {
			const $slider = $section.querySelector(sliderSelector);
			if (!$slider) return;
			const $slides = $slider.querySelectorAll('.swiper-slide');
			if (!$slides.length) return;
			const $scrollBar = $slider.querySelector(scrollbar);
			if (!$scrollBar) return;

			const slidesCounter = $slides.length <= 3;

			if (slidesCounter) {
				$scrollBar.classList.add('swiper-scrollbar--small_mod');
			} else {
				$scrollBar.classList.remove('swiper-scrollbar--small_mod');
			}

			let isMobile;
			let teamSwiper;

			const initSlider = () => {
				teamSwiper = new Swiper($slider, {
					modules: [Scrollbar],
					speed: 800,
					observer: true,
					observeParents: true,
					scrollbar: {
						el: `${scrollbar}`,
						draggable: true,
					},

					breakpoints: {
						320: {
							slidesPerView: 1.15,
						},
						480: {
							slidesPerView: 1.6,
						},
						768: {
							slidesPerView: 2.2,
						},
						1024: {
							slidesPerView: 3,
						},
					},
				});
			};

			const toggleSlider = () => {
				isMobile = window.innerWidth < BREAKPOINTS.mediaPoint1;
				if (isMobile && !$slider.classList.contains('swiper-initialized')) {
					initSlider();
				} else if (!isMobile && $slider.classList.contains('swiper-initialized')) {
					teamSwiper.destroy(true, true);
				}
			};

			onWindowResize(toggleSlider);
		}
	});

	spaceFilter(10, 1000, 10);
};

export default venueSpaces;
